<template>
  <div :style="'height:'+containerHeight+'px;overflow-y:auto;'">
     <div :style="'text-align:center;width: 300px; margin-left: auto;margin-right: auto;margin-top:' +containerHeight/3+'px'">
          <div class="appName">Teamvision</div>
          <div class="slogan">让工作更高效！</div>
     </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

export default {
  name: 'HomeUnlogin',
  props: [],
  data () {
    return {
    }
  },
  computed: {
    ...mapGetters(['appBodyHeight', 'userInfo']),
    ...mapGetters('systemglobal', ['createDialogShow']),
    containerHeight: function () {
      return this.appBodyHeight - 110
    },

    routerName: function () {
      return this.$route.name
    }
  },

  methods: {
    ...mapMutations('systemglobal', ['setCreateDialogShow']),

    updateProfiles: function () {

    },

    changeAvatar: function () {

    }
  },

  created: function () {
  },

  mounted: function () {
    this.$cookie.remove('sessionid',{path: '/'})
  },

  watch: {},

  components: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">

   .slogan {
     font-family: '幼圆' !important;
     font-size: 35px !important;
     margin-top: 25px;
     margin-bottom: 25px;
     display: block;
     font-weight: 400;
     text-align: center;
     letter-spacing: 12px;
     color: #323A45;
     opacity: 0.6 !important;
   }
   .appName {
     font-family: 'Dotum' !important;
     font-size: 30px;
     opacity: .4;
     font-weight: 400;
     text-align: center;
     color: #323A45;
     width: 270px;
   }

</style>



